@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700;800&display=swap');

::-moz-selection {
  background: #ff2b47;
  color:#fff;
}

::selection {
  background: #ff2b47;
  color:#fff;
}

* {
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

a, a:focus,  a:visited {
  color: #000;
  text-decoration: none;
}

li {
  list-style: none;
}

body {
  background: #fff;
  color: #000;
  font-family:  'Nanum Gothic', -apple-system, sans-serif;
}

section>h1 {
  font-size: 2em;
  text-align: center;
  padding: 20px 0;
}

#root {
  position: relative;
  background: #f6f6f6;
  z-index: 999;
}


/*Header*/
#header_wrap {
  width: 100%;
}


#intro {
  width: 100%;
  height: 300px;
  text-align: center;
  font-size: 2.2em;
  font-weight: bold;
  word-break: keep-all;
  background: #fcc412;
  padding: 1rem 0 2rem;
}

#intro svg {
  -webkit-transform: scale(1.4);
          transform: scale(1.4);
  -webkit-transform-origin: center top;
          transform-origin: center top;
  -webkit-animation: scaledown 1s ease-out forwards;
          animation: scaledown 1s ease-out forwards;
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
}
@-webkit-keyframes scaledown {
  to {
    -webkit-transform:scale(1);
            transform:scale(1);
  }
}
@keyframes scaledown {
  to {
    -webkit-transform:scale(1);
            transform:scale(1);
  }
}

#intro path, #intro circle {
  stroke-dashoffset: 2500;
  stroke-dasharray: 2500;
  fill:none;
  stroke:#333;
  -webkit-animation: drawline 2.25s ease forwards;
          animation: drawline 2.25s ease forwards;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: center center;
          transform-origin: center center;
}

@-webkit-keyframes drawline {
  50% {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
    fill: transparent;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    fill: #000;
  }
}

@keyframes drawline {

  50% {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
    fill: transparent;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    fill: #000;
  }
}

#intro p {
  opacity: 0;
  -webkit-transform:translateY(250px);
          transform:translateY(250px);
  -webkit-animation: showit 1s ease forwards;
          animation: showit 1s ease forwards;
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
}


#intro p:last-child {
  -webkit-animation-delay: 3.25s;
          animation-delay: 3.25s;
}

@-webkit-keyframes showit {
  to {
    opacity: 1;
    -webkit-transform:translateY(0);
            transform:translateY(0);
  }
}

@keyframes showit {
  to {
    opacity: 1;
    -webkit-transform:translateY(0);
            transform:translateY(0);
  }
}

#logo {
  width: 150px;
  height: 150px;
  position: relatve;
  margin-bottom: -30px;
}


.sup {
  font-size: 1rem;
  margin-top: .8rem;
}


#mewnuWrap {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: auto;
  height: 100%;
  z-index: 9999;
  background: #fcc412;
}

.menus {
  width: 100%;
  height: 100%;
  text-align: center;
}

.menus li{
  text-align: center;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  color:#fff;
  font-weight: 500;
}

.menus li a {
  width: 50px;
  display: block;
  padding: 34px 15px;
  font-size: 1.245rem;
  letter-spacing: .5rem;
  -webkit-transition: .2s ease all;
  transition: .2s ease all;
}

.menus li a.active {
  background: #000;
  color: #fff;
  font-weight: 700;
}

.menus li:hover a{
  background: #444;
  color: #fff;
}

#Projects {
  width: 100%;
  background: #f6f6f6;
  position: relative;
  z-index: 10;
}

.ptfListWrap {
  width: 980px;
  max-width: 100%;
  min-height: 500px;
  margin: 0 auto;
}

.ptfListWrap li {
  width: 300px;
  max-width: 100%;
  padding: 0;
  margin-bottom: 30px;
  background: #fff;
  overflow: hidden;
  color:#000;
  -webkit-transition: .25s ease all;
  transition: .25s ease all;
  -webkit-transform-origin: center center;
          transform-origin: center center;
} 

.ptfListWrap li:hover {
  box-shadow: 8px 8px 10px rgba(4, 3, 3, 0.1);
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}

.ptfListWrap li img {
  width: 100%;
  height: auto;
}

.ptfDesc {
  padding: .75em;
}

.hash span {
  display: inline-block;
  width:auto;
  height: 2rem;
  line-height: 1.85rem;
  background: #fcc412;
  color:#000;
  border-radius: 10px;
  margin-right: 5px;
  padding: 1px 10px;
  font-size: .9rem;
  letter-spacing: -.25px;
  font-weight: 900;
}

.hash span::before {
  content: '#';
  display: inline-block;
  margin-right: 3px;
}

.ptfWhereamI {
  font-size: 1.325rem;
  font-weight: 700;
  margin-bottom: 12px;
}

.ptfComp {
  font-size: 1rem;
  font-weight: 500;
  margin-bottom: 10px;
}

.ptfProjectDesc {
  font-size: 1rem;
  line-height: 1.4em;
  font-weight: 500;
  margin-bottom: 10px;
}

.ptfDesc button {
  display: block;
  width: 100%;
  margin-top: 18px;
  font-size: 1rem;
  background: #eee;
  color:#000;
  padding: 10px 0;
  -webkit-transition: .5s ease all;
  transition: .5s ease all;
  outline: none;
  cursor: pointer;
}

.ptfDesc button:hover {
  background: #ff2b47;
  color:#fff;
}

#Career {
  width: 100%;
  background: #fff;
  padding: 3rem 0;
  position: relative;
  z-index: 999;
}

#Career>ul {
  width: 690px;
  max-width: 100%;
  margin: 0 auto;
  position: relative;
}

#Career>ul::before {
  content: '';
  width: 20px;
  height: 100%;
  display: block;
  position: absolute;
  top:0;
  left: 0;
  background: #fcc412;
  border-radius: 20px;
}

#Career>ul>li{
  position: relative;
  padding-left: 5rem;
  margin-bottom: 3.4rem;
}

#Career>ul>li::before {
  content: '';
  width: 8px;
  height: 8px;
  display: block;
  position: absolute;
  top:.75em;
  left: 6px;
  background: #000;
  border-radius: 20px;
}

#Career>ul>li>ul {
  padding-left: 1.4rem;
}

#Career>ul>li>ul>li {
  margin-bottom: .4rem;
}

#Career h2 {
  font-size: 1.325rem;
  color:#000;
  margin-bottom: .8rem;
}

#Career h2 span {
  font-size: 1rem;
  font-weight: 500;
  padding-left: 15px;
}

#Career li.subs {
  margin-left: 1rem;
  position: relative;
  padding-left: .75em;
}

#Career li.subs::before {
  content: '';
  color: #676767;
  width: 2px;
  height: 2px;
  border-radius: 50%;
  position: absolute;
  top:.75em;
  left: 0;
  background: #000;
  display: block
}

#Review {
  width: 100%;
  background: #fcc412;
  position: relative;
  z-index: 999;
}

#Review article {
  width: 980px;
  margin:0 auto;
  padding:40px 0;
  line-height: 2.4rem;
  font-weight: 600;
}

#Review span {
  background: #000;
  color: #fff;
  display: inline-block;
  padding: 4px 7px;
  border-radius: 20px;
  line-height: 1.2rem;
}

#footer {
  text-align: right;
  background: #222;
  padding: .75rem 1rem;
  color:#fcc412;
}

.toggleDiv table {
  width: 100%;
  border-collapse:collapse;
  margin-top: 10px;
}

.toggleDiv table caption {
  display: none;
}

.toggleDiv table th, .toggleDiv table td {
  padding:10px 0;
  color: #000;
}

.toggleDiv table th {
  border-bottom: 2px solid #333;
  text-align: center;
}

.toggleDiv table td {
  padding-left: 10px;
  border-bottom: 1px solid #333;
  text-align: left;
}

.toggleDiv table td:first-child {
  text-align: center;
}

.viewmore .btnView {
  width: 200px;
  height: 50px;
  line-height: 50px;
  background: #fcc412;
  text-align: center;
  color: #000;
  font-weight: bold;
  cursor: pointer;
  border-radius: 20px;
  margin-top: 20px;
  outline: none;
  font-size: 1.245rem;
}

.toggleDiv table td {
  text-align: center;
}
.toggleDiv table td:first-child {
  text-align: left;
}
.toggleDiv table th {
  background: #fcc412;
}
